
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import AttendanceList from '@/components/attendance/AttendanceList'
import LingLoading from '@/components/attendance/LingLoading'
export default {
name: 'TeacherList',
components: {
Group,
Cell,
AttendanceList,
Flexbox,
FlexboxItem,
LingLoading,
Divider
},
data() {
return {
showContent: [],
titleArr: [
'姓名', '班级', '职位', '到校', '离校', ''
]
}
},
methods: {
handleDetail: function() {
return

},
fetchData: function(formData) {

let user = localStorage.getItem('CURRENT_USER')
this.$store.dispatch('actionStaffAttendance', {...{date: this.getSelectTime, schoolId: JSON.parse(user).schoolId}, ...formData})
}
},
computed: {
getLoadingState: function() {
return this.$store.getters.getLingLoading
},
/**
* 获取职工身份对应表
* @return {Object} 身份表
*/
getIdentity: function() {
return this.$store.getters.getIdentity
},
/**
* 组装职工列表
* @return {Array} 组装好的职工信息表
*/
getStaffAttendanceList: function() {
let arr = [], list = this.$store.getters.getStaffAttendance.list, identity = this.getIdentity
for(let i in list) {
arr.push({
name: list[i].name,
className: list[i].type == 32?list[i].title?list[i].title:'无':'无',
position: identity[list[i].type],
in_time: list[i].in_time?list[i].in_time:'****',
out_time: list[i].out_time?list[i].out_time:'****',
})
}

return  arr
},
/**
* 获取选择的时间
* @return {String}
*/
getSelectTime: function() {
return this.$parent.$data.selectTime
}
},
watch: {
/**
* 监听显示时间的变化
* @param time
*/
getSelectTime: function(time) {
this.fetchData({date: time})
}
},
created() {
this.fetchData()
}
}



